/* BUTTONS */

/* Button styling */
/* Un-themed */
button,
.btn,
[type="submit"] {
  display: block;
  line-height: 2rem;
  overflow: hidden;
  padding: 0.5rem 1.5rem;
  width: auto;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  cursor: pointer;
  text-align: center;
  transition: all 0.1s;
  background-color: #fefefe;
  border-color: #d9d9d9;
  color: #737373;
  font-size: 0.75rem;
  letter-spacing: 0.03rem;
  text-transform: uppercase;
  font-family: Montserrat;
  min-width: 2rem;
  user-select: none;
}

button:hover,
.btn:hover,
[type="submit"]:hover {
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  -moz-transition: all 0.1s;
  background: #eee;
}

.btn:active,
button:active,
[type="submit"]:active {
  -webkit-transition: 0.1s ease;
  transition: 0.1s ease;
  background: #ddd;
}

/* Make the loading circle, if present, white when button is active */
.btn.loading:active:after,
button.loading:active:after,
[type="submit"].loading:active:after {
  -webkit-transition: 0.1s ease;
  transition: 0.1s ease;
  border: 2px solid #fff;
  border-radius: 50%;
  border-right-color: transparent;
  border-top-color: transparent;
}

button.btn-animated:active,
[type="submit"].btn-animated:active,
.btn.btn-animated:active {
  -webkit-transform: scale(0.99);
  transform: inherit, scale(0.99);
}

.btn:focus,
button:focus,
input[type="submit"]:focus {
  outline: none;
  border: 1px solid #d9d9d9;
  box-shadow: 0 0 0 0.2rem rgba(217, 217, 217, 0.5);
}

.btn.btn-accent:focus,
button.btn-accent:focus,
[type="submit"].btn-accent:focus {
  border-color: rgba(240, 61, 77, 0.45);
  box-shadow: 0 0 0 0.2rem rgba(240, 61, 77, 0.5);
}

.btn.btn-outline-inverted:focus,
button.btn-outline-inverted:focus,
[type="submit"].btn-outline-inverted:focus,
.btn.btn-black:focus,
button.btn-black:focus,
[type="submit"].btn-black:focus,
.btn.btn-dark:focus,
button.btn-dark:focus,
[type="submit"].btn-dark:focus {
  outline: none;
  border: 1px solid #222;
  box-shadow: 0 0 0 0.2rem rgba(52, 58, 64, 0.5);
}

.btn.btn-transparent:focus,
button.btn-transparent:focus,
[type="submit"].btn-transparent:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(217, 217, 217, 0.5);
}

.btn:disabled,
button:disabled,
[type="submit"]:disabled {
  cursor: not-allowed;
  background-color: #d7d7da; /* Make it darker than the other input colors */
  color: #fff;
}

.btn:disabled:hover,
button:disabled:hover,
[type="submit"]:disabled:hover {
  background-color: #d7d7da; /* Make it darker than the other input colors */
}

button.btn-close {
  background-color: rgba(10, 10, 10, 0.2);
  border: none;
  border-radius: 290486px;
  cursor: pointer;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  height: 20px;
  outline: 0;
  position: relative;
  vertical-align: top;
  width: 20px;
  padding: 0;
  min-width: 20px;
}

button.btn-close:hover {
  background-color: rgba(10, 10, 10, 0.3);
}

/* Render the x in the close button */
button.btn-close::before,
button.btn-close::after {
  background-color: var(--cirrus-bg);
  content: "";
  display: block;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

button.btn-close::before {
  height: 2px;
  width: 50%;
}

button.btn-close::after {
  height: 50%;
  width: 2px;
}

/* Accent button disabled */
.btn-accent:disabled {
  background-color: var(--cirrus-accent-bg);
  color: #fff;
  border-color: #c21b2b;
  opacity: 0.6;
}

.btn-accent:disabled:hover {
  background-color: var(--cirrus-accent-bg);
  color: #fff;
  border-color: #c21b2b;
}

/* Adds small margin around buttons */
.btn-container {
  margin: 0.5rem;
  overflow: visible;
}

/* Different styles */

/* Outline (white) */
.btn-outline {
  color: #fff;
  background: transparent;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  border: 1px solid #fff;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.btn-outline:hover,
.btn-outline:active {
  background: rgba(255, 255, 255, 0.35);
  color: #fff;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

/* Inverted outline (dark) */
.btn-outline-inverted {
  border: 1px solid #222;
  color: #222;
  background: transparent;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

.btn-outline-inverted:hover,
.btn-outline-inverted:active {
  color: #fff;
  background: #222;
}

/* Transparent Background */
.btn-transparent {
  background: transparent;
  color: #999;
  border: 1px solid transparent;
}

.btn-transparent:hover {
  background: rgba(0, 0, 0, 0.1);
  color: #999;
}

.btn-transparent:active {
  background: rgba(0, 0, 0, 0.3);
  color: #f5f5f5;
  transform: none;
}

/* Light gray */
.btn-light {
  background: #eee;
  color: #333;
}

.btn-light:hover {
  background: #e0e0e0;
}

.btn-light:active {
  background: #dbdbdb;
}

/* Dark Gray */
.btn-dark {
  background: #444;
  border: 1px #444 solid;
  color: #fff;
}

.btn-dark:hover {
  background: #3a3a3a;
}

.btn-dark:active {
  background: #303030;
}

/* Black */
.btn-black {
  background: #000;
  color: #fff;
}

.btn-black:hover {
  background: #000;
}

.btn-black:active {
  background: #000;
}

/* Right of input */
.has-controls.input:not([class*="left action"]) > button:last-child,
.has-controls.input:not([class*="left action"]) > .btn:last-child > .btn {
  border-radius: 0 0.28571429rem 0.28571429rem 0;
}

.has-controls.input:not([class*="right action"]) > button:last-child,
.has-controls.input:not([class*="right action"]) > .btn:last-child > .btn {
  border-radius: 0.28571429rem 0 0 0.28571429rem;
}

/* Tiny button */
.btn-tiny {
  line-height: 1.5rem;
  padding: 0 0.25rem;
  font-size: 50%;
}

/* Small button */
.btn-small {
  line-height: 2.4rem;
  padding: 0 0.5rem;
  font-size: 70%;
}

/* Big button */
.btn-large {
  line-height: 3.5rem;
  padding: 0 2rem;
  font-size: 90%;
}

.btn-xlarge {
  line-height: 4.5rem;
  padding: 0 2rem;
  font-size: 110%;
}

.btn-xxlarge {
  line-height: 4.5rem;
  padding: 0 2rem;
  font-size: 120%;
}

/* Make the loader white so it is visible */
.loading.btn-accent:after {
  border: 2px solid #fff;
  border-right-color: transparent;
  border-top-color: transparent;
}

/* Override default behavior of aligning font awesome icons to the top */
button .fa,
.btn .fa,
[type="submit"] .fa {
  vertical-align: baseline;
}

button .fa.pad-right,
.btn .fa.pad-right,
[type="submit"] .fa.pad-right {
  margin-right: 0.4rem;
}

button .fa.pad-left,
.btn .fa.pad-left,
[type="submit"] .fa.pad-left {
  margin-left: 0.4rem;
}

/* Alternatives to pad-left and pad-right */
button span:first-child,
.btn span:first-child,
[type="submit"] span:first-child {
  margin-right: 0.2rem;
}

button span:last-child,
.btn span:last-child,
[type="submit"] span:last-child {
  margin-left: 0.2rem;
}

/* Button with accent */
.btn-accent {
  background-color: var(--cirrus-accent-bg);
  color: #fff;
  border-color: var(--cirrus-accent-border);
}

.btn-accent:hover {
  background-color: var(--cirrus-accent-hover);
}

.btn-accent:active {
  background-color: var(--cirrus-accent-border);
}

/* Success Button */
.btn-success {
  color: #fff;
  background-color: #00cb52;
  border-color: #00ad46;
}

.btn-success:hover {
  background-color: #00b147;
}

.btn-success:active {
  background-color: #00b147;
}

/* BUTTON GROUPS */
/* Will group buttons with components in a single component */

.btn-group {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; /* Wrap contents to next row on overflow */
  display: -webkit-inline-box;
  display: inline-flex;
  display: -ms-inline-flexbox;
  display: -webkit-inline-flex;
}

.btn-group.btn-group-fill {
  /* Makes the button group fill the width and proportion the buttons to fill the space */
  display: -webkit-box;
  display: flex;
  display: -ms-flexbox;
  display: -webkit-flex;
}

.btn-group .btn,
.btn-group button,
.btn-group [type="submit"] {
  -ms-flex: 1 0 auto;
  -webkit-box-flex: 1;
  flex: 1 0 auto;
  margin: 0;
}

.btn-group.btn-group-fill .btn,
.btn-group.btn-group-fill button,
.btn-group.btn-group-fill [type="submit"] {
  -ms-flex: 1 0 0;
  -webkit-box-flex: 1;
  flex: 1 0 0; /* Make the buttons fill the available width and proportion themselves */
}

.btn-group .btn:first-child:not(:last-child),
.btn-group button:first-child:not(:last-child),
.btn-group [type="submit"]:first-child:not(:last-child) {
  /* Style the first child in group > 1 buttons */
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.btn-group .btn:last-child:not(:first-child),
.btn-group button:last-child:not(:first-child),
.btn-group [type="submit"]:last-child:not(:first-child) {
  /* Style the last child in group > 1 buttons */
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -0.1rem;
}

.btn-group .btn:not(:first-child):not(:last-child),
.btn-group button:not(:first-child):not(:last-child),
.btn-group [type="submit"]:not(:first-child):not(:last-child) {
  /* Style button in middle of group */
  border-radius: 0; /* Remove roundness from center buttons */
  margin-left: -0.1rem;
}
