/* TABLE */

.table {
  margin-bottom: 1.5rem;
  width: 100%;
  border-collapse: collapse; /* Allows us to set 0 margin in table cells */
  border-spacing: 0;
  text-align: center; /* Text center by default */
}

.table td, .table th {
    border: 1px solid rgba(219, 219, 219, 0.50);
  border-width: 0 0 1px;
  padding: 0.75rem;
  vertical-align: top;
  text-align: inherit;
  margin: 0;
}

.table tr {
  transition: all .3s;
}

.table caption {
  padding-top: .75rem;
  padding-bottom: .75rem;
  color: #6c757d;
  text-align: left;
  caption-side: bottom;
}

.table tr:hover, .table.striped tbody tr:nth-child(even):hover {
  background-color: rgba(216, 216, 216, 0.15);
}

/* Thicker border for table header */
.table thead th, .table thead {
  border-bottom: 2px solid rgba(219, 219, 219, 0.49);
}

.table.bordered thead th, .table.bordered thead {
  border-bottom: 1px solid rgba(219, 219, 219, 0.50);
}

.table thead th, .table tfoot th {
    padding: 1rem;
}

.table tfoot th {
  border-top: 2px solid rgba(219, 219, 219, 0.50);
  border-bottom: none;
}

/* CUSTOM STYLES */
/* All borders */
.table.bordered td, .table.bordered th {
  border: 1px solid rgba(219, 219, 219, 0.50);
}

.table.bordered thead td, .table.borderd thead {
  border-width: 1px;
}

/* Striped table */
.table.striped tbody tr:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.05);
}


/* Decrease padding */
.table.small td, .table.small th {
  padding: 0.25rem 0.75rem;
}

/* Fixed title table */
.table.fixed-head thead {
    position: relative;
  display: block;
}

.table.fixed-head tbody {
    height: 200px; /* Set the height you want */
  display: block; /* Needed */
  overflow: auto; /* Shows scrollbars */
}

.table.fixed-head tr {
  display: table;
  width: 100%; /* Forces row to span container */
}

.table tr.selected { /* Style a selected row */
  background-color: var(--cirrus-accent-bg);
  color: #fff;
}

/* Borderless Table (for inner cells) */
.table.borderless thead th,
.table.borderless th,
.table.borderless td {
  border: none;
}
